<template>
  <div>
    left

     getList

    <h3 style="color: red;" v-show="show">{{fromName}}getList</h3>

    <button @click="emitToEvent('getResult',list)">getResult</button>

    <ul>
      <li v-for="v in list" :key="v">{{v}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "left",
  data(){
    return {
      show: false,
      list: [],
      fromName: null
    }
  },
  methods:{
    emitEvent(from,eName,eParams){
      if(eName==='getList'){
        //
        this.show = true
        this.list = eParams;
        this.fromName = from.component;
      }
    },
    emitToEvent(eName,eParams){
      this.$emit('emitEvents',eName,eParams); // //
    }
  }
}
</script>

