<template>
  <div>
    right

     getResult

    <h3 style="color: red;" v-show="show">{{fromName}}getResult</h3>

    <button @click="emitToEvent('getList',list)">getList</button>
  </div>
</template>

<script>
export default {
  name: "right",
  data(){
    return {
      show: false,
      list: ['right1','right2'],
      fromName: null
    }
  },
  methods:{
    /***
     * 
     * @from ()
     * @eName ()
     * @eParams ()
     * ***/
    emitEvent(from,eName,eParams){
      if(eName==='getResult'){
        //
        this.show = true;
        this.fromName = from.component;
      }
    },
    /***
     * 
     * @eName ()
     * @eParams ()
     * ***/
    emitToEvent(eName,eParams){
      this.$emit('emitEvents',eName,eParams); //
    }
  }
}
</script>

